import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    nav:'0',
    userName:'系统管理员',
    loginStata:'false',
    dataArr:[],
    imgNum:'1',
  },
  mutations: {
    setTest(state,data){
      this.state.nav = data;
    },
    setData(state,data){
      this.state.dataArr = data;
    },
    setUserName(state,data){
      this.state.userName = data;
    },
    setLoginState(state,data){
      this.state.loginStata = data;
    },
    setImgNum(state,data){
      this.state.imgNum = data;
    },
  },
  actions: {},
  modules: {},
});
