<template>
  <div id="login">
    <div class="login-app">
        <iframe style="width:100%;height:100%;background:#fff" src="./二维码生成.html" frameborder="0"></iframe>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginData:{
          name:'',
          password:''
      },
      rules_add:{
        name:[{ required: true, message: '请输入 用户名'}],
        password:[{ required: true, message: '请输入 密码'}],
      },
      checked:false,
    }
  },
  methods:{
    login(data){
      this.$refs[data].validate((valid) => {
        if (valid == true) {
          // this.$router.push('/home')
          window.fonnon.login(this.loginData.name, this.loginData.password)
          setTimeout(() => {
            let logindt=this.$store.state.dataArr
            // console.log(this.$store.state.dataArr,1199199);
            if(logindt=='true' || logindt==true){
              // sessionStorage.setItem('name',this.loginData.name)
              
              if(this.checked==true){
                localStorage.setItem('loginState',this.checked)
                localStorage.setItem('loginName',this.loginData.name)
                sessionStorage.setItem('loginName1',this.loginData.name)
                localStorage.setItem('loginPassword',this.loginData.password)
                this.$store.commit("setLoginState",'true');
              }else{
                localStorage.setItem('loginState',this.checked)
                localStorage.setItem('loginName','')
                sessionStorage.setItem('loginName1',this.loginData.name)
                localStorage.setItem('loginPassword','')
              }
              this.$store.commit("setUserName", this.loginData.name);
              // console.log(this.$store.state.userName);
              this.$router.push('/home')
              this.$message({
                type: 'success',
                message: '登录成功!'
              });
            }else{
              this.$message.error('用户名或密码错误');
            }
          }, 500);
        }
      })
    }
  },
  mounted(){
    if(localStorage.getItem('loginState')=='true'){
      this.checked=true
    }else{
      this.checked=false
    }
    this.$store.commit("setLoginState",'false');
    this.loginData.name=localStorage.getItem('loginName')
    this.loginData.password=localStorage.getItem('loginPassword')
    sessionStorage.setItem('loginName','')


    this.clientHeight = document.documentElement.clientHeight;
    const that = this;
    // 安卓手机键盘吊起挡住输入框
 
    window.onresize = function() {
 
      if(document.documentElement.clientHeight < that.clientHeight) {
        // scrollVal为负值
        let scrollVal = document.documentElement.clientHeight-that.clientHeight;
        $(".alert-main").css("marginTop",scrollVal);
        $(".bottom-create").hide();
      }else {
        $(".alert-main").css("marginTop",0);
        $(".bottom-create").show();
      }
      
    };
  }
}
</script>

<style scoped lang="less">
* {
  margin: 0;
  padding: 0;
}
#login {
    width: 100%;
    height: 100%;
    /deep/.el-form-item__error{
      font-size: 14px;
    }
    .login-app{
        width: 100%;
        height: 100%;
        background-image: url(../assets/images/bg.jpg);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        position: relative;
        text-align: center;
        display: flex; 		/*行内元素为inline-flex*/
    }
    .login_box{
        width: 60%;
        height: 40%;
        display: inline-block;
        min-width: 400px;
        min-height: 380px;
        max-width: 500px;
        max-height: 400px;
        border-radius: 15px;
        background: #e0e0e0a1;
        margin: auto;
        h2{
            width: 100%;
            height: 65px;
            font-size: 32px;
            text-align: center;
            border-radius: 15px 15px 0 0;
            line-height: 65px;
            background: #005650d5;
            font-weight: 400;
            color: #fff;
            opacity: 0.7;
        }
    }
    .loginData{
        width: 80%;
        margin: auto;
        margin-top: 25px;
        overflow: hidden;
        padding-bottom: 18px;
        /deep/.el-input__inner{
            height: 55px;
            border-radius: 5px;
            font-size: 16px;
            text-indent: 2em;
        }
        /deep/.el-form-item{
          margin-top: 20px;
          margin-bottom: unset !important;
        }
        .input_imag{
          width: 36px;
          position: absolute;
          left: 6px;
          top: 9px;
        }
    }
    .internalButton{
      width: 80%;
      padding: 15px 0;
      font-size: 17px;
      color: #fff;
      background-color: #005650 !important;
      border: none;
    }
    .pawss{
      width: 80%;
      line-height: 20px;
      text-align: left;
      margin: auto;
      margin-bottom: 10px;
      color: #000;
    }
    /deep/ .is-checked {
    .el-checkbox__input {
      .el-checkbox__inner {
        background-color: #0a8a55;
        border-color: #0a8a55;
      }
    }
    /deep/.el-checkbox__label {
      color: #0a8a55;
    }
  }
}
.bbb{
  position: fixed;
  bottom: 20px;
  left: 30px
}
</style>
<style lang="less" scoped>
  @import "../assets/css/asstr.less";
